
// 路由懒加载  当这个路由被访问时 才去编译加载这个路由组件模板

import Vue from 'vue'

import VueRouter from 'vue-router';

Vue.use(VueRouter) //全局声明

const routes = [
  { path: "/", redirect: { name: 'home' } },
  {
    path: "/guide",
    name: "guide",
    component: () => import("@/views/Guide.vue"),
  },
  {
    path:"/search",
    name:"search",
    component:()=>import("@/views/Search.vue")
  },
  {
    path: "/good/:goodId",
    name: "good",
    component: () => import("@/views/Good.vue"),
  },
  {
    path: "/login",
    name: "login",
    component: () => import("@/views/Login.vue"),
  },
  {
    path: "/loginPasswd",
    name: "loginPasswd",
    component: () => import("@/views/LoginPasswd.vue")
  },
  {
    path: "/register",
    name: "register",
    component: () => import("@/views/Register.vue")
  },
  {
    path: "/index",
    name: "index",
    component: () => import("@/views/Index.vue"),
    children: [
      { path: "/", redirect: { name: 'home' } },
      { path: "home", name: "home", component: () => import("@/views/Home.vue") },
      { path: "classify", name: "classify", component: () => import("@/views/Classify.vue") },
      { path: "cart", name: "cart", component: () => import("@/views/Cart.vue") },
      { path: "mine", name: "mine", component: () => import("@/views/Mine.vue") },
    ]
  },
  {
    path: "**",
    redirect: { name: 'home' }
  }

]

const router = new VueRouter({
  routes,
  mode: "hash",
  base: "/"

})
export default router;