<template>
  <van-nav-bar
    :title="title"
    :left-arrow="show"
    @click-left="onClickLeft"
    
  >
    
    
 
</van-nav-bar>
</template>

<script>
export default {
  data() {
    return {
        searchShow:true,
    };
  },
  props: {
    title: {
        type:String,
        default: ""
    },
    show:{
        type:Boolean,
        default:false
    }
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight(){

    },
  
  },
  mounted() {
    if (this.$route.name=="search") {
     
      this.searchShow = false;
     
    }
  }
};
</script>